.menu-bg {
  width: 1000px;
  height: 300px;
  border-radius: 100%;
  background: #fff;
  position: absolute;
  top: -176px;
  left: -96px;
}
.menu-list {
  height: 0;
  overflow-y: hidden;
  z-index: 4;
  position: absolute;
  top: 28px;
  width: 300px;
  line-height: 30px;
  margin-left: 13px;
  color: #707070;
  padding-left: 2px;
}
.topMenus-div-style {
  cursor: pointer;
}
.topMenus-div-style:hover .menu-list {
  height: 180px;
  transition: all 1s;
  z-index:1000;
}
.menu-item-box {
  position: relative;
}
.menu-item-box::before {
  content: "";
  width: 1px;
  height: 53px;
  background: #707070;
  position: absolute;
  left: 0px;
  top: -35px;
  z-index: 1;
}
.menu-item {
  padding-left: 25px;
  position: relative;
  cursor: pointer;
}
.menu-item::before {
  content: "";
  width: 20px;
  height: 1px;
  background: #707070;
  position: absolute;
  left: 0px;
  top: 14px;
}
.menu-item::after {
  content: "";
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #707070;
  position: absolute;
  left: -2px;
  top: 12px;
}
.menu-list .menu-item-box:nth-child(1) {
  padding-top: 20px;
}

@media screen and (max-width: 1780px) {
  .topMenus-div-style:hover .menu-item {
    background: #fff;
    border-radius: 4px;
    padding-right: 10px;
  }
}
.icon-size {
  width: 12px;
  height: 12px;
}