@-webkit-keyframes scale-up-top {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
}
@keyframes scale-up-top {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
}

.target .target-tip {
  display: none;
}

.home-page-sub .target .target-name {
  display: block;
}

.home-page-sub .target .target-tip span {
  display: block;
  line-height: 26px;
  width: 100%;
}

.home-page-sub .target .target-tip {
  display: none;
  width: 150px;
  height: 100px;
  position: absolute;
  background: rgb(255, 255, 255);
  top: -50px;
  right: 20%;
  border-top-right-radius: 55px;
  border-top-left-radius: 40px;
  border-bottom-right-radius: 39px;
  border-bottom-left-radius: 40px;
  text-align: center;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: center;
  z-index: 100;
}

.home-page-sub .target .target-tip::after {
  content: "";
  width: 0;
  height: 0;
  border-width: 30px 7px 0;
  border-style: solid;
  border-color: #fff transparent transparent;
  position: absolute;
  bottom: -24px;
  right: 28px;
  z-index: -1;
}

.home-page-sub .target .target-tip::before {
  content: "";
  width: 0;
  height: 0;
  border-width: 75px 30px 0px 0px;
  border-style: solid;
  border-color: #fff transparent transparent;
  position: absolute;
  bottom: -24px;
  left: 28px;
  z-index: -1;
}

.home-page-sub .target:hover .target-tip {
  display: flex;
  transition: all 1s ease;
}

.home-page-sub .target-tip-left {
  left: 0 !important;
}

.home-page-sub .btn-page-sub-close {
  position: absolute;
  width: 34px;
  height: 34px;
  top: 92px;
  right: 140px;
  cursor: pointer;
  z-index: 10;
}
