.page-bar-student {
  top: 30%;
}
@media screen and (max-width: 1600px) {
  .page-bar-student {
    top: 30%;
  }
}
.gradual-student {
  background: -webkit-gradient(
    linear,
    0 0,
    0 100%,
    from(transparent),
    to(rgba(255, 251, 172, 1))
  );
}
.relation-item-student-one {
  background-color: #f8b003;
}
.relation-item-student-two {
  background-color: #fd9a13 !important;
}
.tutor-list-right-span-selected {
  background: #f09b63 !important;
  color: #ffffff;
}
.tutor-list-item-student1,
.student-list-item-nor,
.student-list-item-nox {
  background-color: #f9c95b !important;
}
.tutor-list-item-student2,
.tutor-list-left-R,
.tutor-list-left-x {
  background-color: #fbd99b !important;
}
.student-total-item {
  border: 3px solid #f7b002;
}
.page-item-six-item-student:nth-child(1) {
  background-color: #f8b003;
}

.page-item-six-item-student:nth-child(2),
.page-item-six-item-student:nth-child(3) {
  background-color: #fd9a13;
}
.page-item-six-item-student:nth-child(4) {
  background-color: #fbb383;
}
.student-list-right {
  margin-left: 6px;
}
.student-list-right span {
  margin-left: 6px;
}

.tutor-list-title .student-list-right span {
  margin-left: 30px;
}
.page-item-five-box-student {
  color: #fff;
  font-size: 14px;
}
.page-item-five-box-student .page-item-five-item:nth-child(even) {
  background-color: #f8b003;
}
.page-item-five-box-student .page-item-five-item:nth-child(odd) {
  background-color: #fd9a13;
}
.page-item-five-box-student .page-item-five-item:nth-child(4) {
  background-color: #fbb383;
}
.page-flex-box-student {
  display: flex;
  flex-wrap: nowrap;
  font-size: 14px;
}
.page-flex-box-student-item {
  width: 24%;
  padding: 20px;
  border: 3px solid #f7b002;
  border-radius: 20px;
  background-color: #fdffec;
  margin-right: 20px;
}
.tutor-list-student {
  margin-top: 130px;
}
.student-item-title-two {
  padding: 0 44px;
  color: #000;
  line-height: 24px;
}
.relation-item-student-ye {
  background-color: #f8b003 !important;
}
.relation-item-student-be {
  background-color: #fd9a13 !important;
}
.block {
  display: block;
}
