.about-box {
    border: 3px solid #eca677;
    border-radius: 20px;
    background-color: #ffecdc;
    padding: 30px;
    line-height: 24px;
}

.about-box div span {
    color: #D8793A;
}

.page-two-item {
    display: flex;
    align-items: center;
    margin-top: 30px;
    position: relative;
}

.page-two-item-title {
    color: #FFFFFF;
    padding: 20px 40px;
    width: 30%;
    border-radius: 30px;
}

.page-two-item:nth-child(odd) .page-two-item-title,
.page-two-item:nth-child(odd) .pop,
.page-two-item:nth-child(odd) .pop::after,
.page-two-item:nth-child(odd) .pop::before {
    background-color: #F0B463;
}

.page-two-item:nth-child(even) .page-two-item-title,
.page-two-item:nth-child(even) .pop,
.page-two-item:nth-child(even) .pop::after,
.page-two-item:nth-child(even) .pop::before {
    background-color: #F09B63;
}

.page-two-item:nth-child(even) .page-two-item-title {
    width: 35%;
    margin-left: 20px;
}

.page-two-item-title strong {
    font-size: 34px;
}

.page-two-item-title span {
    font-size: 18px;

}

.page-two-item-title span:nth-child(1) {
    transform: translate(0, 4px);
}

.page-two-item-title span:nth-child(2) {
    display: inline-block;
    transform: rotate(180deg) translate(0, -6px);
}

.page-two-item-value {
    width: 70%;
    margin-left: 30px;

}

.page-two-item-value span {
    font-weight: bold;
    color: rgb(39, 39, 39);
}

.page-two-item .pop {
    position: absolute;
    width: 30px;
    height: 30px;
    border-radius: 50%;

}

.page-two-item:nth-child(odd) .pop {
    left: -12px;
}

.page-two-item:nth-child(even) .pop {
    left: 8px;
}

.page-two-item .pop::after {
    content: "";
    position: absolute;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    left: -10px;
    top: 24px;
}

.page-two-item .pop::before {
    content: "";
    position: absolute;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    left: -18px;
    top: 35px;
}

.page-three-pop {
    position: relative;
    height: 500px;
}

.page-three-item {
    position: absolute;
    border-radius: 50%;
    display: flex;
    align-content: center;
    flex-wrap: wrap;
}

.page-three-item:nth-child(1) {
    width: 250px;
    height: 250px;
    top: 100px;
    left: -50px;
}

.page-three-item:nth-child(2) {
    width: 210px;
    height: 210px;
    top: 0;
    left: 188px;
}

.page-three-item:nth-child(3) {
    width: 200px;
    height: 200px;
    top: 200px;
    left: 323px;
}

.page-three-item:nth-child(4) {
    width: 250px;
    height: 250px;
    top: 18px;
    left: 508px;
}

.page-three-item:nth-child(5) {
    width: 200px;
    height: 200px;
    top: 211px;
    left: 716px
}

@media screen and (max-width: 1600px) {
    .page-three-item:nth-child(1) {
        width: 220px;
        height: 220px;
        top: 100px;
        left: -50px;
    }

    .page-three-item:nth-child(2) {
        width: 190px;
        height: 190px;
        top: 0;
        left: 150px;
    }

    .page-three-item:nth-child(3) {
        width: 190px;
        height: 190px;
        top: 200px;
        left: 252px;
    }

    .page-three-item:nth-child(4) {
        width: 220px;
        height: 220px;
        top: 18px;
        left: 380px;
    }

    .page-three-item:nth-child(5) {
        width: 190px;
        height: 190px;
        top: 211px;
        left: 520px
    }
}

.page-three-item:nth-child(odd),
.page-three-item:nth-child(odd) .pop,
.page-three-item:nth-child(odd) .pop::after,
.page-three-item:nth-child(odd) .pop::before {
    background-color: #F09B63;
}

.page-three-item:nth-child(even),
.page-three-item:nth-child(even) .pop,
.page-three-item:nth-child(even) .pop::after,
.page-three-item:nth-child(even) .pop::before {
    background-color: #F0B463;
}

.page-three-item-title {
    color: #000;
    padding: 0 30px;
    line-height: 30px;
}

.page-three-item-value {
    color: #fff;
    padding: 0 30px;
