.page-item-five-box-tutor .page-item-five-item:nth-child(odd) {
  background-color: #fdd998;
}
.page-item-five-box-tutor .page-item-five-item:nth-child(even) {
  background-color: #fbd09e;
  margin-left: 30px;
}
.tutor-collect {
  margin-top: 20px;
  background-color: #fbd09e;
  border-radius: 16px;
  display: flex;
  padding: 16px;
}
.tutor-collect-left {
  width: 65%;
  padding-right: 20px;
  border-right: 1px solid #4f4f50;
}
.tutor-collect-right {
  padding-left: 20px;
}
.tutor-pop {
  position: relative;
  height: 460px;
}
.tutor-pop-item {
  position: absolute;
  border-radius: 50%;
  display: flex;
  align-content: center;
  flex-wrap: wrap;
  width: 160px;
  height: 160px;
  color: #fff;
  padding: 20px 20px;
}
.tutor-pop-item:nth-child(1) {
  background-color: #f0b463;
  top: 164px;
  left: -50px;
}
.tutor-pop-item:nth-child(2) {
  top: 0;
  left: 150px;
  background-color: #f09b63;
}
.tutor-pop-item:nth-child(3) {
  top: 212px;
  left: 275px;
  background-color: #d8793a;
}
.tutor-pop-item:nth-child(4) {
  background-color: #f0b463;
  top: 20px;
  left: 429px;
}
.tutor-pop-item:nth-child(5) {
  top: 242px;
  left: 540px;
  background-color: #f09b63;
}
.tutor-pop-item:nth-child(6) {
  top: 38px;
  left: 711px;
  background-color: #d8793a;
}
@media screen and (max-width: 1600px) {
  .tutor-pop-item:nth-child(1) {
    left: -50px;
  }
  .tutor-pop-item:nth-child(2) {
    left: 93px;
  }
  .tutor-pop-item:nth-child(3) {
    left: 178px;
  }
  .tutor-pop-item:nth-child(4) {
    left: 300px;
  }
  .tutor-pop-item:nth-child(5) {
    left: 399px;
  }
  .tutor-pop-item:nth-child(6) {
    left: 505px;
  }
}
.tutor-pop-item:nth-child(1)::after,
.tutor-pop-item:nth-child(3)::after,
.tutor-pop-item:nth-child(5)::after {
  content: "";
  position: absolute;
  width: 150px;
  border-bottom: 3px dashed #d8793a;
  z-index: -1;
  top: -24px;
  right: -96px;
  transform: rotate(147deg);
}
.tutor-pop-item:nth-child(2)::after,
.tutor-pop-item:nth-child(4)::after {
  content: "";
  position: absolute;
  width: 150px;
  border-bottom: 3px dashed #d8793a;
  z-index: -1;
  bottom: -49px;
  right: -38px;
  transform: rotate(-123deg);
}
.tutor-list {
  margin-top: 80px;
}
.tutor-list-item {
  border-radius: 20px;
  height: 46px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  margin-bottom: 20px;
}
.tutor-list-title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding: 0 20px 10px 20px;
}
.tutor-list-left {
  display: flex;
}
.tutor-list-num {
  width: 24px;
  font-size: 16px;
  font-weight: bold;
}
.tutor-list-right {
  display: flex;
}
.tutor-list-right span {
  display: block;
  width: 38px;
  height: 38px;
  border-radius: 50%;
  margin-left: 42px;
  background: #fff;
  font-size: 14px;
  font-weight: bold;
  line-height: 38px;
  text-align: center;
}
.tutor-list-item:nth-child(odd) {
  background-color: #fad48e;
}
.tutor-list-item:nth-child(even) {
  background-color: rgb(250, 196, 135);
}
.tutor-list-title .tutor-list-right {
  margin-right: 10px;
}
.tutor-list-title .tutor-list-right span {
  background: transparent;
  word-wrap: break-word;
  width: 15px;
  line-height: 16px;
  margin-left: 66px;
  text-align: end;
  display: flex;
  align-items: end;
}
.tutor-list-right span:hover {
  background: #f09b63;
  color: #ffffff;
}
.tutor-list-right-span-selected {
  background: #f09b63 !important;
  color: #ffffff;
}
.gradual-tutor {
  background: -webkit-gradient(
    linear,
    0 0,
    0 100%,
    from(transparent),
    to(rgba(253, 223, 172, 1))
  );
}
.tutor-list-left-title {
  color: #393939;
  font-weight: 600;
  display: flex;
  align-items: flex-end;
}
