.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  background-color: #FFF2DC;
  color: #707070;
  height: 100%;
}

#root {
  height: 100%;
}

.page-item {
  margin-bottom: 140px;
}

.page-item-title {
  margin: 0 0 20px 0;
  font-size: 34px;
}
.page-item-banner{
  width: 100%;
  margin: 30px 0 10px;
  height: 228px;
}

.page-detail-info {
  border: 1px solid transparent;
  width: 100%;
  height: 100%;
  padding: auto;
  box-sizing: border-box;
}

.page-item-info {
  color: #6D6E70;
  font-size: 14px;
  line-height: 24px;
  margin: 20px 0;
}

.page-item-sub {
  color: #393939;
  font-weight: 500;
  font-weight: 600;
}
.page-item-sub-title{
  color: #393939;
  font-weight: 600;
  font-size: 14px;
  margin: 40px 0 20px 0;
}

.page-bar {
  position: fixed;
  top: 40%;
  left: 16%;
  width: 130px;
  z-index: 8;
}

.page-cloud-l {
  width: 54px !important;
  height: 40px !important;
  left: 6% !important;
  top: 171px !important;
}

.page-cloud-m {
  width: 100px !important;
  height: 69px !important;
  right: 13% !important;
}

.page-cloud-r {
  width: 54px !important;
  height: 40px !important;
  right: 10% !important;
  top: 172px !important;
}

.page-close {
  width: 34px;
  height: 34px;
  position: absolute;
  top: 100px;
  right: 20%;
  z-index: 10;
}

.page-detail-foot {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 80px;
  z-index: 4;
}

.detail-foot {
  position: absolute;
  width: 504px;
  height: 193px;
  right: -200px;
  bottom: 0;
  background: url('https://gongyuuxuan.oss-cn-beijing.aliyuncs.com/floor.png') no-repeat;
  background-size: cover;
}

.detail-floor {
  position: absolute;
  z-index: 5;
  width: 2834px;
  height: 262px;
  bottom: -180px;
  left: -386px;
  background: url('https://gongyuuxuan.oss-cn-beijing.aliyuncs.com/foot.png') no-repeat;
  background-size: cover;
}
.gradual{
  width: 100%;
    height: 80px;
    position: fixed;
    bottom: 20px;
}
.gradual-our{
  background: -webkit-gradient(linear, 0 0, 0 100%, from(transparent), to(rgba(253, 231, 200, 1)));
}
.gradual-about{
  background: -webkit-gradient(linear, 0 0, 0 100%, from(transparent), to(rgba(255, 225, 199, 1)));
}
.bg-wave {
  position: fixed;
  border-radius: 100%;
  width: 312px;
  height: 447px;
  bottom: -228px;
  right: 23px;
  z-index: 0;
}

.bg-wave-our::before {
  content: '';
  position: absolute;
  border-radius: 100%;
  width: 202px;
  height: 382px;
  top: -62px;
  left: 247px;
  z-index: 0;
}

.bg-wave-our::after {
  content: '';
  position: absolute;
  border-radius: 100%;
  width: 242px;
  height: 318px;
  top: 32px;
  right: 257px;
  z-index: 0;
}

.bg-wave,
.bg-wave-our::before,.bg-wave-our::after {
  background: -webkit-gradient(linear, 0 0, 0 50%, from(rgba(255, 255, 255, 0.3)), to(transparent)) !important;
}

.bg-wave-about,
.bg-wave-about::before,.bg-wave-about-l,.bg-wave-about-l::after {
  background: -webkit-gradient(linear, 0 0, 0 50%, from(rgba(255, 178, 130, 0.3)), to(transparent)) !important;
}
.bg-wave-about::after {
  display: none;
}
.bg-wave-about-l::after{
  content: '';
  position: absolute;
  border-radius: 100%;
  width: 258px;
  height: 331px;
  top: 97px;
  left: 307px;
  z-index: 0;
}

.bg-wave-l {
  width: 429px;
  height: 443px;
  bottom: -228px;
  left: -186px;
}
.left-ban{
  right: 10%;
}
@media screen and (max-width: 1600px) {
  .left-ban{
    right: 2%;
  }
}

.bg-wave-about,
.bg-wave-about::before,
.bg-wave-about::after {
  background: -webkit-gradient(linear, 0 0, 0 100%, from(rgba(255, 255, 255, 0.3)), to(transparent));
}

.questions-item{
  border-radius: 20px;
  padding: 10px 20px;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.questions-name{
  margin-right: 6px;
}
.questions-item:nth-child(even){
  background:#f5d59a
}
.questions-item:nth-child(odd){
  background:#f3cb96
}
.questions-box-line{
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 10px;
}
.questions-an{
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background-color: #fff;
  display: inline-block;
  margin-left: 16px;
  cursor: pointer;
}
.questions-an-active span{
  display: block;
  width: 100%;
  height: 100%;
  background: url(./assets/img/detail/relation/ok.svg) no-repeat;
  background-size: cover;
}

@media screen and (max-width: 1600px) {
  .page-bar{
      left: 13%;
  }
}