.page-three-flex-box-strile{
    display: flex;
    margin-bottom: 20px;
} 
.page-three-flex-box-strile div{
    width: 50%;
    padding: 20px;
    border: 3px solid #45CE50;
    border-radius: 20px;
    background-color: #edffec;
    margin-right: 20px;
}

.page-item-five-box-strile .page-item-five-item:nth-child(odd) {
    background-color: #d1f1b5;
}
.page-item-five-box-strile .page-item-five-item:nth-child(even) {
    background-color: #bef9c3;
    margin-left: 30px;
}
.sterile-pop{

}
