.single-page-three-pop{
    height: 440px;
}
.single-page-three-item:nth-child(odd),
.single-page-three-item:nth-child(odd) .pop,
.single-page-three-item:nth-child(odd) .pop::after,
.single-page-three-item:nth-child(odd) .pop::before {
    background-color: #F27081;
}

.single-page-three-item:nth-child(even),
.single-page-three-item:nth-child(even) .pop,
.single-page-three-item:nth-child(even) .pop::after,
.single-page-three-item:nth-child(even) .pop::before {
    background-color: #FF867C;
}
.single-page-three-item:nth-child(3),
.single-page-three-item:nth-child(3) .pop,
.single-page-three-item:nth-child(3) .pop::after,
.single-page-three-item:nth-child(3) .pop::before {
    background-color: #FF895D;
}
.gradual-single{
    background: -webkit-gradient(linear, 0 0, 0 100%, from(transparent), to(rgba(250, 210, 210, 1)));
}